.GearEditor {
    display: grid;
    grid-template-rows: auto;
    grid-template-areas: 
      "gearName gearName gearName gearName gearName gearName"
      "gearimage brandimage mainImage subArea subArea subArea"
      "seedArea seedArea seedArea seedArea seedArea seedArea"
      "newGearArea newGearArea newGearArea newGearArea newGearArea newGearArea"
      "control control control control control save";
      justify-items: center;
      align-items: center;
      gap: 5px;
}

@media (max-width: 800px) {
    .GearEditor {
        grid-template-rows: auto;
        grid-template-areas: 
        "gearName"
        "gearimage"
        "brandimage"
        "mainImage"
        "subArea" 
        "seedArea"
        "newGearArea"
        "control"
        "save";
        overflow: auto;
        max-height: 500px;
    }
}


.GearImage {
    grid-area: gearimage;
}

.BrandImage {
    grid-area: brandimage;
    justify-content: center;
}

.MainAbility {
    grid-area: mainImage;
    --ability-with: 64px;
}

.abilityImage {
    background-color: black;
    border-radius: 50%;
    width: var(--ability-with);
    height: var(--ability-with);
}

.GearName {
    grid-area: gearName;
}

.Seed {
    grid-area: seedArea;
}

.NewGearArea {
    grid-area: newGearArea;
}

.Subs {
    grid-area: subArea;
    overflow:auto;
    border: 1px solid black;
    width: 100%;
    height: 100%;
    max-height: 310px;
}

.Control {
    grid-area: control;
    justify-self: start;
}

.abilitiyList {
    --ability-with: 32px;
    display: flex;
    gap: 4px;
    flex-flow: row wrap;
    padding: 5px;
}

.abilityHeader {
    font-weight: bold;
    padding-left: 5px;
    padding-bottom: 5px;
}

.Save {
    grid-area: save;
}

.GearList {
    display: flex;
    flex-flow: row  wrap;
    gap: 10px 10px;
}

.GearListElem {
    align-self: auto;
    border: 1px solid black;
}

.Button {
    color: #ffffff;
    background-color: #007bff;
    border-color: #007bff;
    cursor: pointer;
    text-align: center;
    vertical-align: middle;
    border: 1px solid transparent;
    padding: .375rem .75rem;
    font-size: 1rem;
    line-height: 1.5;
    border-radius: .25rem;
    margin-right: 10px;
}

.ButtonDelete {
    color: #ffffff;
    background-color: rgb(200, 60, 60);
    border-color: rgb(200, 60, 60);
    cursor: pointer;
    text-align: center;
    vertical-align: middle;
    border: 1px solid transparent;
    padding: .375rem .75rem;
    font-size: 1rem;
    line-height: 1.5;
    border-radius: .25rem;
    margin-right: 10px;
}

.Button:hover {
    background-color: #6cb3ff;
}

.ButtonDelete:hover {
    background-color: rgb(140, 42, 42);
}


@media (max-width: 550px) {
    .Button {
        margin-left: 5px;
        margin-top: 5px;
        font-size: .65rem;
    }
}

.Table {
    border: 1px solid #dee2e6;
    border-collapse: collapse;
    width: 100%;
    color: #212529;
    text-align: left;
}

.TableHead {
    padding: .75rem;
    border: 1px solid #dee2e6;
    text-align: center;
}

.ContentNotFound {
    text-align: center;
    background-color: rgba(0,0,0,.05);
}

.TDEven {
    background-color: rgba(0,0,0,.05);
    text-align: center;
}

.TDOdd {
    background-color: #dee2e6;
    text-align: center;
}

.TableAbility {
    --ability-with: 36px;
}